.homeBg-container{
    padding: 20px;
}

.heading-des-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.homeHeading{
    font-weight: 400;
    font-size: 47px;
}

@media screen and (max-width: 768px){
    .homeHeading{
        font-size: 24px;
    }
}

.description{
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    width: 550px;
}

@media screen and (max-width: 768px){
    .description{
        font-size: 14px;
        width: 100%;
    }
}

.filters-recommendations-totalItems-container{
    padding: 40px;
    padding-top: 0px;
    padding-bottom: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.filters-recommendations-container{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.totalItems{
    font-weight: 700;
    font-size: 16px;
    padding-top: 10px;
}

@media screen and (max-width: 768px){
    .totalItems{
        display: none;
    }
}